.advertisement_conatiner {
  height: 400px;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  align-items: center;
  width: 100%;
  position: relative;
  padding-top: 50px;
}

.bowl_wave {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  z-index: -1;
}

.bowl_wave svg {
  height: 100%;
}

.advertisement_conatiner img {
  width: 100%;
  height: 100%;
}
.moblie_img {
  width: 340px;
  height: 340px;
}
.moblie_img img{
  width: 100%;
  height: 100%;
}
.adver_content {
  display: flex;
  flex-direction: column;
  height: 150px;
  justify-content: space-around;
  width: 558px;
}

.adver_content span:nth-child(1) {
  font-weight: var(--font-weight);
  font-size: 36px;
  line-height: 40px;
  font-weight: bold;
  text-transform: none;
  margin: 0px 0px 8px;
}

.adver_content span:nth-child(2) {
  font-size: 18px;
  line-height: 20px;

  font-size: 16px;
    line-height: 20px;
    letter-spacing: 0px;
}
.social_btns {
  display: flex;
}
.social_btns a img {
  width: 135px;
  height: 45px;
  margin-left: 5px;
  margin-top: 15px;
}

@media screen and (min-width: 600px) and (max-width: 992px) {
  .advertisement_conatiner {
    margin-top: 100px;
    flex-direction: column;
    justify-content: space-between;
    height: 600px;
  
  }
  .moblie_img {
    width: 90%;
    height: 390px;
  }
  .adver_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 48px;
  }
  .bowl_wave {
    position: absolute;
    bottom: -50%;
  }
}
